/** Media Queries **/
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}

.imagen {
  height: 25rem;
  background-image: url(../img/1.jpg);
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .imagen {
    height: auto;
  }
}

.app {
  margin: 0 auto;
  width: 95%;
  padding: 3rem 0;
}

.nombre__app {
  font-weight: 700;
  margin: 5rem 0;
}

.descripcion__app p {
  margin: 0.5rem;
}
.descripcion__app p:last-child {
  margin-bottom: 5rem;
}

.barra {
  border-bottom: 3px solid #0da6f3;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

.seccion {
  display: none;
}

.mostrar {
  display: block;
}

.contenedor-resumen p {
  color: #FFFFFF;
}
.contenedor-resumen span {
  color: #0da6f3;
  font-weight: bold;
}
.contenedor-resumen h3 {
  margin-top: 5rem;
}

.contenedor-servicio {
  border-bottom: 1px solid #e1e1e1;
  padding: 3rem 0;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}